















































































import { Component, Vue, Watch } from 'vue-property-decorator'

import ErrorDialog from '@/components/ErrorDialog.vue'
import CarModel from '@/components/CarModel.vue'
import AgvTable from '@/components/AgvTable.vue'
import Battery from '@/components/public/Battery.vue'
import { Get } from '@/utils/request'

import { moduleMsg } from '@/mock/data/moduleMsg'

@Component({
    components: {
        Battery,
        ErrorDialog,
        CarModel,
        AgvTable
    }
})
export default class AGV extends Vue {
    errMsg = ''
    electricity: number = 0
    isShowError = false
    carName: string = ''
    carNumber: string = ''
    carVersion: string = ''
    batteryStatus: string = ''
    moduleInfo: any = {
        WheelFront: { status: undefined, msg: '' },
        WheelEnd: { status: undefined, msg: '' },
        CollisionFront: { status: undefined, msg: '' },
        CollisionEnd: { status: undefined, msg: '' },
        TOFFront: { status: undefined, msg: '' },
        TOFEnd: { status: undefined, msg: '' },
        SonicFront: { status: undefined, msg: '' },
        SonicEnd: { status: undefined, msg: '' },
        RFIDFront: { status: undefined, msg: '' },
        RFIDEnd: { status: undefined, msg: '' },
        Battery: { status: undefined, msg: '' }
    }

    taskName: string = ''
    latestPosition: string = ''
    speed: number = 0
    get progress () {
        return `${this.speed * 100}%`
    }

    tableData: Array<any> = []
    carStatus: number = 1001
    wifiShow: boolean = false
    @Watch('carStatus')
    wifiStatus (val: any) {
        switch (val) {
        case 1000:
            this.wifiShow = true
            break
        case 1001:
        case 1002:
            this.wifiShow = false
            this.$notify({
                type: 'danger',
                message: '当前小车掉线或出现异常，请检查网络连接或排除故障！'
            })
            break
        case 1003:
            this.wifiShow = true
            this.$notify({
                type: 'primary',
                message: '当前小车空闲'
            })
            break
        }
    }

    mounted () {
        this.$nextTick(() => {
            const battery = (this.$refs.battery) as Battery
            battery.init()
            this.getCarInfo()
            this.getCarDynamic()
        })
    }

    onConfirmError () {
        this.isShowError = false
    }

    getCarInfo () {
        const model = ['', 'Battery', 'TOF', 'Deviate', 'Wheel', 'Collision', 'RFID', 'Sonic']
        const nameReflect: any = {
            Battery: '电池模块',
            TOF: 'TOF模块',
            Deviate: '磁感模块',
            Wheel: '轮子模块',
            Collision: '碰撞模块',
            RFID: 'RFID模块',
            Sonic: '超声波模块'
        }
        Get('/carinfo').then((res: any) => {
            this.carName = res.name
            this.carNumber = res.carNumber
            this.carVersion = res.version
            this.carStatus = res.carStatus
            this.electricity = parseFloat(res.electricity)
            this.batteryStatus = moduleMsg[res.electricStatus]
            res.moduleList.forEach((item: any) => {
                const moduleName = item.type === 1 ? 'Battery' : `${model[item.type]}${item.modId === '1' ? 'Front' : 'End'}`
                this.moduleInfo[moduleName] = {
                    status: item.info.status % 1000,
                    msg: `${nameReflect[model[item.type]]}: ${moduleMsg[item.info.status]}`
                }
            })
            setTimeout(() => {
                this.getCarInfo()
            }, 10000)
        }).catch(() => {
            setTimeout(() => {
                this.getCarInfo()
            }, 10000)
        })
    }

    getCarDynamic () {
        Get('/cart_dynamic').then((res: any) => {
            this.tableData = []
            this.speed = res.speed
            this.taskName = res.taskName
            this.latestPosition = res.actualPath[res.actualPath.length - 1].pos
            res.tarTask.forEach((item: any, index: number) => {
                const actual = index >= res.actualPath.length
                const temp = {
                    target: item.pos,
                    actual: actual ? '' : res.actualPath[index].pos,
                    statusCode: actual ? '' : (item.pos === res.actualPath[index].pos ? 0 : 1),
                    distance: actual ? '' : res.actualPath[index].distance,
                    time: actual ? '' : res.actualPath[index].time
                }
                this.tableData.push(temp)
            })
            setTimeout(() => {
                this.getCarDynamic()
            }, 10000)
        }).catch(() => {
            setTimeout(() => {
                this.getCarDynamic()
            }, 10000)
        })
    }
}
