
























import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class AgvTable extends Vue {
    @Prop({ type: Array, default: () => [] }) data!: Array<any>

    getPathColor (row: any) {
        if (row.statusCode === 0) {
            return 'agv-green'
        } else if (row.statusCode === 1) {
            return 'agv-red'
        } else if (row.statusCode === 2) {
            return 'agv-gray'
        } else {
            return ''
        }
    }
}
