










import { Component, Watch, Prop, Vue } from 'vue-property-decorator'

@Component
export default class Battery extends Vue {
    @Prop({ type: String, default: '剩余电量：' }) batteryStatus!: string
    @Prop({ type: Number }) electricCapacity!: number
    capacityWidth: number = 14
    @Watch('electricCapacity')
    changeBatteryIcon () {
        const cap = document.querySelector('.capacity') as HTMLDivElement
        if (this.electricCapacity <= 0.2) {
            cap.style.backgroundColor = '#F05051'
        } else {
            cap.style.backgroundColor = '#6DD400'
        }
        cap.style.width = `${this.electricCapacity * this.capacityWidth}px`
    }

    get batteryCapacity () {
        return `${this.electricCapacity * 100}%`
    }

    init () {
        this.changeBatteryIcon()
    }
}
