


































































































import { Component, Prop, Vue } from 'vue-property-decorator'
import ErrorDialog from '@/components/ErrorDialog.vue'

@Component({
    components: {
        ErrorDialog
    }
})
export default class CarModel extends Vue {
    @Prop({ type: String, default: '--' }) msg!: string
    // @Prop({ type: Function }) onConfirmError!: any
    @Prop({ type: Object }) moduleInfo!: object

    errMsg = ''
    isShowError = false

    getModuleColor (statusCode: number) {
        if (statusCode === 0) {
            return '#6DD400'
        } else if (statusCode === 1) {
            return '#B0AEAE'
        } else if (statusCode >= 2) {
            return '#F05051'
        } else {}
    }

    onClickCarModule (info: any) {
        this.showErrorDialog(info.msg)
    }

    showErrorDialog (msg: string) {
        this.errMsg = msg
        this.isShowError = true
    }

    onConfirmError () {
        this.isShowError = false
    }
}
